<template>
  <div class="home">
    <div class="container my-5 text-capitalize">
      <h1>add new todos</h1>
      <div class="todo-form w-75 mx-auto shadow p-4 rounded-4">
        <form @submit.prevent="addTodo">
          <label for="">enter your todo</label>
          <!-- ? add todo -->
          <input
            required
            type="text"
            v-model="todoObject.text"
            class="form-control"
            placeholder="add todo"
          />
          <div
            class="timeAndDate row my-3 d-flex justify-content-center align-items-center"
          >
            <!-- !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! data starts (from)!!!!!!!!!!!!!!!!!!!!!!!! -->
            <div class="col-md-6">
              <div class="fromTime">
                <label for="">from</label>
                <input
                  required
                  v-model="todoObject.from"
                  type="date"
                  class="form-control"
                  placeholder="add todo"
                />
              </div>
            </div>
            <!-- !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! data starts (to)!!!!!!!!!!!!!!!!!!!!!!!! -->

            <div class="col-md-6">
              <div class="toTime">
                <label for="">to</label>
                <input
                  required
                  v-model="todoObject.to"
                  type="date"
                  class="form-control"
                  placeholder="add todo"
                />
              </div>
            </div>
          </div>

          <input
            type="submit"
            class="btn btn-info my-3 text-capitalize"
            value="add"
          />
        </form>
      </div>
    </div>
  </div>
</template>

<script setup>
//  imports
import { ref, onMounted } from "vue";

const todoList = ref([]);
const todoObject = ref({
  id: "",
  text: "",
  from: "",
  to: "",
  createdAt: "",
  isCompleted: false,
});

import { createToaster } from "@meforma/vue-toaster";
const toaster = createToaster({
  position: "top",
  max: 1,
});

// methods

// -----------------------------------------> add todo method
const addTodo = () => {
  todoObject.value.id = todoList.value.length + 1;
  todoObject.value.createdAt = new Date();
  todoList.value.push(todoObject.value);
  addToLocalStorage();
  toaster.success(`todo added successfully`);
  console.log(todoList.value);
  todoObject.value = {
    id: "",
    text: "",
    from: "",
    to: "",
    createdAt: "",
    isCompleted: false,
  };
};

// -----------------------------------------> add to local storage----------------------------------------
const addToLocalStorage = () => {
  localStorage.setItem("todos", JSON.stringify(todoList.value));
};

// ----------------------------------check local storage
const checkLocal = () => {
  if (localStorage.getItem("todos")) {
    todoList.value = JSON.parse(localStorage.getItem("todos"));
  }
};

onMounted(() => {
  checkLocal();
});
</script>
<style scoped lang="scss">
.container {
  h1,
  label {
    font-family: "Cairo", sans-serif;
  }
}
</style>
